import {
  ButtonGroup,
  ToggleButton,
  Badge,
  Button,
  Offcanvas,
  OffcanvasBody,
  InputGroup,
  Form,
} from "react-bootstrap";
import { useState } from "react";
import CanvasDisplay from "./canvasDisplay";

function MemeEditor({ filePassed }) {
  const [topTextValue, setTopTextValue] = useState(undefined);
  const [btmTextValue, setBtmTextValue] = useState(undefined);
  const [showSettings, setShowSettings] = useState(false);
  const [watermark, setWatermark] = useState(true);
  const [fontSize, setFontSize] = useState(70);
  const [textMargin, setTextMargin] = useState(10);
  const [borderSize, setBorderSize] = useState(0);

  const handleTopText = (e) => {
    setTopTextValue(e.target.value);
  };

  const handleBottomText = (e) => {
    setBtmTextValue(e.target.value);
  };

  const handleToggleSetting = () => {
    setShowSettings(!showSettings);
  };

  return (
    <>
      <InputGroup className="settingElement">
        <Form.Control placeholder="Enter top text." onChange={handleTopText} />
      </InputGroup>

      <InputGroup className="settingElement">
        <Form.Control
          placeholder="Enter bottom text."
          onChange={handleBottomText}
        />
      </InputGroup>

      <div id="imageBox">
        <CanvasDisplay
          filePassed={filePassed}
          topText={topTextValue}
          btmText={btmTextValue}
          passedFontSize={fontSize}
          passedFontMargin={textMargin}
          passedBorderSize={borderSize}
          passedWaterMark={watermark}
        />
      </div>

      <div className="d-grid gap-2">
        <Button
          variant="outline-secondary"
          onClick={handleToggleSetting}
          size="lg"
          className=" m-2"
        >
          Settings
        </Button>
      </div>
      <Offcanvas
        show={showSettings}
        onHide={handleToggleSetting}
        placement="bottom"
      >
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Settings</Offcanvas.Title>
        </Offcanvas.Header>
        <OffcanvasBody>
          <div>
            <h5>Font Control</h5>
            <hr />
            <div>
              <div>
                Font size -
                <Button
                  className="settingElement m-2"
                  onClick={() => {
                    setFontSize(fontSize - 1);
                  }}
                >
                  -
                </Button>
                <Badge
                  bg="secondary"
                  className="settingElement"
                  style={{ fontSize: "17px" }}
                >
                  {fontSize}
                </Badge>
                <Button
                  className="settingElement m-2"
                  onClick={() => {
                    setFontSize(fontSize + 1);
                  }}
                >
                  +
                </Button>
              </div>
              <div>
                Font margin -
                <Button
                  className="settingElement m-2"
                  onClick={() => {
                    setTextMargin(textMargin - 1);
                  }}
                >
                  -
                </Button>
                <Badge
                  bg="secondary"
                  className="settingElement"
                  style={{ fontSize: "17px" }}
                >
                  {textMargin}
                </Badge>
                <Button
                  className="settingElement m-2"
                  onClick={() => {
                    setTextMargin(textMargin + 1);
                  }}
                >
                  +
                </Button>
                <hr />
              </div>
            </div>
          </div>
          <div>
            <h5>Border Control</h5>
            <hr />
            <div>
              <div>
                Border size -
                <Button
                  className="settingElement m-2"
                  onClick={() => {
                    setBorderSize(borderSize - 1);
                  }}
                >
                  -
                </Button>
                <Badge
                  bg="secondary"
                  className="settingElement"
                  style={{ fontSize: "17px" }}
                >
                  {borderSize}
                </Badge>
                <Button
                  className="settingElement m-2"
                  onClick={() => {
                    setBorderSize(borderSize + 5);
                  }}
                >
                  +
                </Button>
              </div>
            </div>
          </div>
          <hr />
          <ButtonGroup className="mb-2">
            <ToggleButton
              id="toggle-check"
              type="checkbox"
              variant="outline-danger"
              checked={watermark}
              onChange={() => setWatermark(!watermark)}
            >
              Watermark
            </ToggleButton>
          </ButtonGroup>
        </OffcanvasBody>
      </Offcanvas>
    </>
  );
}

export default MemeEditor;
