import { Button, Form, InputGroup } from "react-bootstrap";
function FileOpener(props) {
  const handleChange = (e) => {
    if (e.target.files && e.target.files.length > 0) {
      //setImgFile(URL.createObjectURL(e.target.files[0]));
      sendUpdate(URL.createObjectURL(e.target.files[0]));
    }
  };

  const handleFetch = () => {
    //setImgFile(document.getElementById("fetchField").value);
    sendUpdate(document.getElementById("fetchField").value);
  };

  const handleOnSelect = () => {
    document.getElementById("fileSelect").click();
  };

  const sendUpdate = (u) => {
    props.onUpdate({ u });
  };

  return (
    <>
      <input
        id="fileSelect"
        placeholder="enter image url here"
        type="file"
        accept="image/*"
        onChange={handleChange}
        hidden={true}
      ></input>

      <InputGroup className="settingElement">
        <Button onClick={handleOnSelect}>Select File</Button>
        <p className="settingElement"> OR </p>
        <Form.Control
          id="fetchField"
          type="text"
          placeholder="Enter or paste a image URL here."
        />
        <Button onClick={handleFetch}>Fetch</Button>
      </InputGroup>
    </>
  );
}

export default FileOpener;
