import { Button } from "react-bootstrap";
import html2canvas from "html2canvas";
function MemeDownloader() {
  const downloadIMG = (e) => {
    e.preventDefault();
    html2canvas(document.getElementById("shootThis"), {
      allowTaint: false,
      useCORS: true,
      scale: 4,
      quality: 1,
    }).then(function (canvas) {
      //document.body.appendChild(canvas);
      var a = document.createElement("a");
      a.href = canvas.toDataURL(canvas);
      a.download = "yourDankMeme.jpg";
      a.click();
    });
  };

  return (
    <>
      <div className="d-grid gap-2">
        <Button
          onClick={downloadIMG}
          size="lg"
          className=" m-2"
          variant="success"
        >
          Download
        </Button>
      </div>
    </>
  );
}

export default MemeDownloader;
