import React, { Component } from "react";

class CanvasDisplay extends React.Component {
  state = {
    srcImage: Image,
    can: "",
    canRef: "",
    canMaxSize: 256,
    fontSize: 40,
    fontMargin: 5,
    currentFontSize: 0,
    borderSize: 100,
    watermarkText: "Made with : FTCentral.github.io/SimpleMemeMaker",
    enableWaterMark: true,
  };

  handleDrawImage = () => {
    if (this.props.filePassed) {
      //if (this.props.filePassed) {
      //console.log("running rhis " + this.props.filePassed);
      //}
      //this.state.can.drawImage(this.state.srcImage, 0, 0, 100, 100);

      this.state.canRef.width = this.state.canMaxSize;
      this.state.canRef.height = this.state.canMaxSize;

      var hRatio = this.state.canRef.width / this.state.srcImage.width;
      var vRatio = this.state.canRef.height / this.state.srcImage.height;
      var ratio = Math.min(hRatio, vRatio);
      this.state.can.clearRect(
        0,
        0,
        this.state.canRef.width,
        this.state.canRef.height
      );

      this.state.canRef.width = this.state.srcImage.width * ratio;
      this.state.canRef.height =
        this.state.srcImage.height * ratio + this.state.borderSize;
      //Black BG
      this.state.can.beginPath();
      this.state.can.rect(
        0,
        0,
        this.state.canRef.width,
        this.state.canRef.height
      );
      this.state.can.fillStyle = "black";
      this.state.can.fill();

      //Draw image
      this.state.can.drawImage(
        this.state.srcImage,
        0,
        this.state.canRef.height / 2 - (this.state.srcImage.height * ratio) / 2,
        this.state.srcImage.width * ratio,
        this.state.srcImage.height * ratio
      );
      //Text control
      this.state.can.textAlign = "center";

      this.state.can.fillStyle = "white";
      this.calculateFontSize(this.props.topText);

      this.state.can.shadowColor = "black";
      this.state.can.shadowBlur = 0;
      this.state.can.lineWidth = 5;
      //Top
      //stroke
      this.state.can.strokeText(
        this.props.topText,
        this.state.canRef.width / 2,
        this.state.fontMargin + this.state.currentFontSize - 10
      );
      //normal
      this.state.can.fillText(
        this.props.topText,
        this.state.canRef.width / 2,

        this.state.fontMargin + this.state.currentFontSize - 10
      );
      this.calculateFontSize(this.props.btmText);
      //Bottom
      //stroke
      this.state.can.strokeText(
        this.props.btmText,
        this.state.canRef.width / 2,
        this.state.canRef.height - this.state.fontMargin
      );
      //normal
      this.state.can.fillText(
        this.props.btmText,
        this.state.canRef.width / 2,
        this.state.canRef.height - this.state.fontMargin
      );
      //watermark
      if (this.state.enableWaterMark) {
        this.state.can.font = 14 + "px Ariel";

        this.state.can.fillText(
          this.state.watermarkText,
          this.state.canRef.width -
            this.state.can.measureText(this.state.watermarkText).width / 2,
          this.state.canRef.height
        );
      }
    }
  };

  calculateFontSize = (textPassed) => {
    let f = this.state.fontSize;
    do {
      f--;
      this.state.currentFontSize = f;
      this.state.can.font = this.state.currentFontSize + "px Anton";
    } while (
      this.state.can.measureText(textPassed).width > this.state.canRef.width
    );
  };

  componentDidMount() {
    let sSize = window.innerWidth;
    if (sSize > 740) this.state.canMaxSize = 740;
    else this.state.canMaxSize = window.innerWidth;

    const canV = document.createElement("canvas");
    canV.width = 0;
    canV.height = 0;
    this.state.srcImage = new Image();
    this.state.srcImage.crossOrigin = "anonymous";
    canV.setAttribute("className", "memeImageParent");
    canV.setAttribute("id", "shootThis");

    if (document.querySelector("#shootThis")) {
      document.removeChild(document.querySelector("#shootThis"));
      console.log("cloneCanvas bug fix executed");
    }

    this.state.can = canV.getContext("2d");
    this.state.srcImage.onload = this.handleDrawImage;
    document.querySelector("#imageBox").appendChild(canV);
    this.state.canRef = canV;
  }
  componentDidUpdate() {
    if (this.props.filePassed) {
      this.state.enableWaterMark = this.props.passedWaterMark;
      this.state.borderSize = this.props.passedBorderSize;
      this.state.fontSize = this.props.passedFontSize;
      this.state.fontMargin = this.props.passedFontMargin;
      this.state.srcImage.src = this.props.filePassed;
    }
  }

  render() {
    return <></>;
  }
}

export default CanvasDisplay;
